@import "partials/variables";
@import "partials/base";
@import "partials/fonts";

#wrapper {
  font-family: $font-list;
  width: 100%;
  padding: 0 0.5em;
  display: block;
  background-color: $color-white;
}

/* toggle butttons */
#sidebar-toggle-btn {
  display: block;
  position: fixed;
  left: 80%;
  z-index: 100;
  top: 40vh;
  width: 65px;
  height: 100px;
  background-color: $color-white;
  border: 2px solid $color-orange;
  color: $color-orange;
  line-height: 100px;
  text-align: center;
  font-size: 1em;
}

#sidebar-toggle-btn:hover {
  background-color: $color-orange;
  cursor: pointer;
  border: 2px solid $header-color;
  color: $color-white;
  font-weight: bold;
}

#menu-toggle-btn {
  display: block;
  position: fixed;
  top: 0.25em;
  left: 80%;
  z-index: 4;
  width: 20%;
  height: 45px;
  line-height: 45px;
  text-align: center;
  background-color: $color-orange;
  border: 2px solid $color-black;
  color: $color-black;
  font-size: 0.9em;
}

#menu-toggle-btn:hover {
  background-color: $color-black;
  cursor: pointer;
  color: $color-white;
  border: none;
}

/* site header */
#site-header {
  height: 70px;
  width: 100%;
}

/* logo */
#logo {
  width: 60%;
  margin: 0;
  padding: 0;
}

/* site navigation */
#main-nav {
  height: 30px;
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0 0 0.5em;
}

/* signin box */

#signin-box {
  width: auto;
  display: none;
  float: right;
  height: 20px;
}
#signin-box > .signin-box-link {
  display: inline-block;
  height: 100%;
  line-height: 20px;
  width: auto;
  font-size: 0.7em;
  text-decoration: none;
  border-right: 1px solid $login-color;
  text-align: center;
  color: $login-color;
  letter-spacing: 0.01em;
  padding: 0 0.5em;
}
.signin-box-link:hover {
  color: $color-orange;
  font-weight: bold;
}
.signin-box-link:last-of-type {
  border: none;
}

/* inner navigation */
#inner-nav {
  display: none;
  width: 75%;
  padding: 0;
  height: 40px;
  margin: 0;
}
#inner-nav > .inner-nav-item {
  float: left;
  width: auto;
  height: 40px;
  margin-right: 0.2em;
  text-align: center;
  display: block;
}
.inner-nav-link {
  padding: 0 0.5em;
  text-align: center;
  height: 40px;
  color: $color-black;
  line-height: 40px;
  display: block;
  transition: all ease 0.2s;
  text-transform: uppercase;
  font-size: 0.8em;
}
.inner-nav-link:focus {
  background-color: rgb(255 255 255 / 40%);
  text-decoration: none;
  display: block;
  border-radius: 3px 3px 0 0;
  box-shadow: 0 -2px 2px 0 rgb(0 0 0 / 20%);
}
.inner-nav-link:hover {
  background-color: rgb(255 255 255 / 40%);
  text-decoration: none;
  display: block;
  border-radius: 3px 3px 0 0;
  box-shadow: 0 -2px 2px 0 rgb(0 0 0 / 20%);
}
#inner-nav > .selected {
  a:link,
  a:visited,
  a:hover {
    background-color: rgb(255 255 255);
  }
}

/* mobile navigation */
.mobile-nav.is-expanded {
  top: 0;
  display: block;
  width: 95%;
  background: $color-black;
  position: fixed;
  z-index: 4;
  border: 1px solid $color-black;
  height: 290px;
  padding: 1%;
}
.mobile-nav {
  transition: all ease 0.2s;
  display: block;
  width: 95%;
  background: $color-black;
  position: fixed;
  z-index: 4;
  border: 1px solid $color-black;
  height: 290px;
  top: -290px;
  padding: 1%;
}
.mobile-nav .mobile-nav-item {
  width: 100%;
  display: block;
  height: 44px;
  float: none;
  margin-bottom: 0.15em;
}
.mobile-nav-item .mobile-nav-link {
  font-size: 1.1em;
  line-height: 45px;
  width: 100%;
  display: block;
  float: none;
  text-align: center;
  color: $color-black;
  background-color: $color-orange;
  text-decoration: none;
}
.mobile-nav-item .mobile-nav-link:hover {
  text-decoration: none;
  display: block;
  border-radius: none;
  box-shadow: none;
  background-color: $color-black;
  color: $color-white;
  border-bottom: none;
}
.mobile-nav-item .mobile-nav-link:visited {
  background-color: $color-white;
  color: $color-black;
}

/* select search categories */
#search-category {
  display: block;
  height: auto;
  width: 100%;
  padding: 0;
  margin: 1% 0;
  clear: both;
}
.search-category-btn {
  width: 100%;
  font-family: $font-list;
  height: 35px;
  line-height: 35px;
  font-size: 1.25em;
  margin: 0;
  background-color: $color-black;
  color: $color-white;
  border: 0;
  padding: 0;
  text-align: center;
  transition: all ease-in-out 0.2s;
  font-weight: bold;
}
.search-category-btn:hover {
  background-color: $color-white;
  color: $color-black;
  cursor: pointer;
  font-weight: bold;
}
#search-category-form {
  color: $color-white;
  background-color: $color-orange;
  margin: 0;
  padding: 0.5em;
  height: auto;
  font-family: $font-list;
  flex-flow: column;
  justify-content: center;
  gap: 0.5em;
  margin-bottom: 0.5em;
}
#search-category-form > fieldset {
  margin: 0;
}
#search-category-form > fieldset #search-category-label {
  font-size: 1em;
  width: 100%;
  line-height: 35px;
  margin: 0;
  flex: 0 1 auto;
  text-align: left;
  text-transform: uppercase;
  text-indent: 0.1em;
  font-weight: bold;
}
#search-category-form > fieldset ::placeholder {
  color: $color-black;
}
#search-category-form > fieldset .search-category-input {
  font-family: $font-list;
  flex: 0 1 auto;
  width: 100%;
  font-size: 0.85em;
  margin: 0.25em 0;
  color: $content-color2;
  line-height: 35px;
  height: 35px;
}

/* main content */
#main-content {
  height: auto;
  width: 100%;
  margin: 0;
  font-family: $font-list;
}

/* main content header */
#main-content-title {
  background-color: $content-color;
  font-family: $font-list;
  width: 100%;
  margin: 1% 0 0;
  height: 40px;
  color: $color-white;
  font-size: 1.2em;
  text-indent: 1%;
  line-height: 40px;
  text-transform: uppercase;
  font-weight: bold;
}

/* other products */
.other-products {
  margin-top: 2em;
  min-height: 400px;
  width: 100%;
  font-family: $font-list;
  display: flex;
  flex-flow: column;
  align-content: center;
  justify-content: center;
}
.other-products > .other-products-box {
  padding: 0;
  height: auto;
  flex: 0 1 auto;
  width: 75%;
  font-family: $font-list;
  margin: 15px auto;
}
.other-products-box > .other-product-box-img {
  width: 65%;
  display: block;
  margin: 0 auto;
  height: auto;
}
.other-products-box > .other-product-box-caption {
  width: 100%;
  margin: 0;
  padding: 7% 0 0;
  background-image: url("../images/product-info-bg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
  display: flex;
  flex-flow: row wrap;
}
.other-product-box-caption > .other-product-box-title {
  color: $login-color;
  line-height: 25px;
  text-transform: uppercase;
  font-size: 100%;
  margin: 0;
  text-indent: 0.2em;
  flex: 1 0 auto;
}
.other-product-box-caption > .other-product-box-price {
  height: auto;
  line-height: 25px;
  margin: 0;
  font-weight: bold;
  width: 100%;
  text-indent: 0.2em;
  flex: 1 0 auto;
}
/* Price Icon */
.other-product-box-caption > .other-product-cart-icon {
  display: block;
  width: 15%;
  transition: all 0.3s ease;
  position: absolute;
  left: 80%;
  top: 20%;
}
.other-product-box-caption > .other-product-cart-icon:hover {
  transform: rotate(360deg);
  cursor: pointer;
}

/* products */
#product-page-box {
  padding: 0.1em;
  height: auto;
  width: 100%;
  font-family: $font-list;
  margin-bottom: 15px;
  display: flex;
}
#product-page-img {
  width: 40%;
  display: block;
  height: auto;
  padding: 2em;
}
#product-page-caption {
  width: 60%;
  margin: 0;
  padding: 0.5em;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}
#product-page-title {
  line-height: 40px;
  text-transform: uppercase;
  margin: 0;
  text-indent: 0.2em;
}
#product-page-price {
  height: auto;
  line-height: 40px;
  margin: 0;
  font-weight: bold;
  text-indent: 0.2em;
}
/* Price Icon */
#cart-page-icon {
  display: block;
  width: 5%;
}
#cart-page-icon:hover {
  transform: rotate(360deg);
  cursor: pointer;
}
#upvotes-section {
  margin: 0;
}

/* Upvotes */
#upvotes-section > .upvotes-section-btn {
  font-family: $font-list;
  margin: 0;
  height: 45px;
  width: 35%;
  display: block;
  font-size: 1em;
  line-height: 30px;
  background-color: $color-orange;
  color: $color-white;
  border: 0;
  float: right;
}
#upvotes-section > .upvotes-section-btn:hover {
  background-color: $color-white;
  color: $color-orange;
  border: 0;
  cursor: pointer;
  font-weight: bold;
}
#upvotes-section > .upvotes-section-text {
  line-height: 50px;
}

p {
  font-size: 0.85em;
  line-height: 1.6em;
}

/* sidebar */
.contact-sidebar {
  width: 96.5%;
  height: auto;
  display: block;
  font-family: $font-list;
  float: none;
  background-color: $color-white-bg;
  padding: 1%;
  border: 2px $content-color1 solid;
  margin-bottom: 5%;
}
.contact-sidebar .contact-sidebar-header {
  font-size: 1em;
  color: $header-color;
  font-weight: bold;
  background-color: $color-white;
  height: auto;
  line-height: 20px;
  text-indent: 0;
}
.contact-sidebar .contact-sidebar-text {
  width: 100%;
  margin: auto;
  font-family: $font-list;
  font-size: 0.9em;
  line-height: 1.8em;
}

/* contact form container */
#contact-form-section {
  width: 98%;
  height: auto;
  float: none;
  display: block;
}

/* contact form */
#contact-form {
  width: 100%;
  padding: 0;
  font-family: $font-list;
}
#contact-form-fields {
  height: auto;
  display: block;
  padding: 0;
}
#contact-form-fields .contact-form-item {
  height: auto;
  display: block;
  margin: 0 0 0.5em;
  padding: 0;
}
.contact-form-item > label {
  width: 100%;
  line-height: 40px;
  height: auto;
  font-size: 1em;
  text-align: left;
  margin: 0;
  display: flex;
  justify-content: space-between;
}
.contact-form-item > #username,
.contact-form-item > #email {
  width: 75%;
  height: 40px;
}
.contact-form-item > #message {
  width: 75%;
  margin: 0;
  padding: 0;
}
.contact-form-item > #username:focus,
.contact-form-item > #emai:focus,
.contact-form-item > #message:focus {
  background-color: $color-white-bg;
  outline: 0;
}
.contact-form-item > #submit {
  font-family: $font-list;
  margin: 0;
  height: 45px;
  width: 100%;
  display: block;
  font-size: 1.2em;
  line-height: 30px;
  background-color: $color-orange;
  color: $color-white;
  border: 0;
}
.contact-form-item > #submit:hover {
  background-color: $color-white;
  color: $color-orange;
  border: 0;
  cursor: pointer;
  font-weight: bold;
}

/* main inner content */
#main-inner-content {
  width: 100%;
  margin: 0;
  font-family: $font-list;
  float: left;
  padding: 0 0.75em;
}

/* inner content title */
.main-inner-content-title {
  font-weight: bold;
  color: $color-orange;
  font-size: 1em;
  height: auto;
  line-height: 25px;
  margin-bottom: 1%;
}

/* rightside content section */
#right-content-section {
  width: 100%;
  margin: 0 0 20px;
}
/* Search product select */
.search-products {
  margin-bottom: 1em;
}
.search-products-select {
  font-family: $font-list;
  width: 100%;
  font-size: 0.9em;
  margin: 0.5em 0;
  color: $content-color2;
  height: 30px;
}

/* right side page content */
.right-content-section-header {
  height: 40px;
  background: none;
  color: $content-color2;
  margin: 0;
  font-size: 1.2em;
  line-height: 40px;
}
/* front page banner */
#front-page-banner {
  position: relative;
  height: auto;
  width: 100%;
  margin: 0;
  padding: 0.25em;
  border: solid 1px $content-color1a;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 0;
}
#front-page-banner > hgroup {
  grid-area: 1 / 1 / 3 / 9;
  font-family: $font-list;
  width: auto;
  z-index: 3;
  padding-top: 0;
}
#front-page-banner > hgroup h1 {
  font-family: $font-list;
  font-size: 1.4em;
  height: auto;
  line-height: 30px;
  width: 100%;
  font-weight: bold;
  color: $content-color1a;
  letter-spacing: -0.08em;
  text-indent: 0;
  margin: auto;
  display: block;
}
#front-page-banner > hgroup h2 {
  font-size: 60%;
  color: $content-color2;
  font-weight: bold;
  letter-spacing: 0.001em;
  text-transform: uppercase;
  height: auto;
  line-height: 30px;
  width: 100%;
  margin: auto;
  display: block;
}
#front-page-banner-img {
  width: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  grid-area: 2 / 4 / 6 / 9;
}
#front-page-banner-nav {
  grid-area: 2 / 1 / 6 / 4;
  position: relative;
  width: 100%;
  height: auto;
  padding-top: 30px;
}
#front-page-banner-nav #banner-nav-items {
  width: 100%;
  padding: 0;
  list-style: none;
  margin: auto;
}
#btn-div {
  height: 150px;
  width: 100px;
  margin-left: 10px;
  position: relative;
}
#banner-nav-items .banner-nav-item {
  height: auto;
  display: block;
  width: 100%;
}
.banner-nav-link {
  background-color: $color-orange;
  display: block;
  height: auto;
  width: 100%;
  margin-bottom: 0.2em;
  color: $color-white;
  font-size: 0.65em;
  line-height: 22px;
  text-align: center;
  transition: all ease 0.2s;
  text-transform: uppercase;
  border-radius: 5px;
  text-decoration: none;
}
.banner-nav-link:hover {
  background-color: $color-white;
  color: $color-orange;
  font-weight: bold;
  text-align: center;
}

/* Products Boxes */
#right-content-section > .product-boxes {
  height: auto;
  width: 100%;
  font-family: $font-list;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  justify-content: center;
}
/* product box */
.product-boxes .product-box {
  padding: 0.1em;
  height: auto;
  flex: 0 1 auto;
  width: auto;
  font-family: $font-list;
  margin: 15px auto;
}
/* product box image */
.product-box .product-box-img {
  width: 100%;
  display: block;
  margin: 0 auto;
  height: auto;
}
/* product box caption */
.product-box .product-box-caption {
  width: 100%;
  margin: 0;
  padding: 7% 0 0;
  background-image: url("../images/product-info-bg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
  display: flex;
  flex-flow: row wrap;
}
.product-box-title {
  color: $login-color;
  line-height: 25px;
  text-transform: uppercase;
  font-size: 85%;
  margin: 0;
  text-indent: 0.2em;
  flex: 1 0 auto;
}
.product-box-price {
  height: auto;
  line-height: 25px;
  margin: 0;
  font-weight: bold;
  width: 100%;
  text-indent: 0.2em;
  flex: 1 0 auto;
}
.product-box-icon-link {
  width: 15%;
  position: relative;
}
/* Price Icon */
.product-box-icon {
  display: block;
  transition: all 0.3s ease;
  position: absolute;
  left: 80%;
  top: 20%;
}
.product-box-icon:hover {
  transform: rotate(360deg);
  cursor: pointer;
}

/* Wider Products Boxes */
#right-content-section > .larger-product-boxes {
  height: auto;
  width: 100%;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  justify-content: center;
}
.larger-product-box {
  display: flex;
  flex-flow: column;
  width: 100%;
  font-family: $font-list;
  margin-bottom: 15px;
}
.larger-product-box-image {
  width: auto;
  display: block;
  margin: 1em auto;
  height: auto;
}
.larger-product-box-caption {
  width: 100%;
  height: auto;
  margin: 0;
  padding: 1%;
  display: block;
  float: left;
}
.larger-product-box-title {
  background-color: $color-white;
  height: auto;
  line-height: 45px;
  text-indent: 0.5em;
  font-size: 0.9em;
  color: $login-color;
  font-weight: bold;
}
.larger-product-box-text {
  width: 100%;
  height: auto;
  display: block;
  line-height: 1.5em;
  text-indent: 0;
  margin: 0;
  font-size: 0.8em;
  padding: 0;
}
.larger-product-box-price {
  height: auto;
  line-height: 50px;
  font-size: 1em;
  margin: 0;
  font-weight: bold;
  display: block;
  width: 100%;
  text-indent: 1%;
}
.larger-product-box-link {
  display: block;
}
.larger-product-box-icon {
  float: left;
  display: block;
  margin: 0;
  width: auto;
  transition: all 0.3s ease;
}
.larger-product-box-icon:hover {
  transform: rotate(360deg);
  cursor: pointer;
}
.larger-product-box-price:last-of-type {
  color: $login-color;
}

/* pagination search */
#right-content-section > #pag-search {
  width: 45%;
  display: block;
  float: left;
  margin: 0 0 2%;
  padding: 0;
}
.pag-search-select {
  font-family: $font-list;
  width: 50%;
  display: inline-block;
  height: 30px;
  margin: 0 1% 0 0;
  padding: 0;
  line-height: 30px;
  font-size: 0.85em;
}
.pag-search-label {
  float: left;
  width: 39%;
  display: block;
  height: 30px;
  color: $color-black;
  border: 0;
  line-height: 30px;
  font-size: 0.8em;
  text-transform: uppercase;
}
.pag-search-label:hover {
  background-color: $color-white;
  color: $color-orange;
  border: 0;
  cursor: pointer;
}

/* pagination */
#right-content-section > #pagination {
  height: 30px;
  width: 35%;
  display: block;
  margin: 0 0 2%;
  float: right;
}
.pagination-link {
  color: $color-white;
  background-color: $color-orange;
  text-align: center;
  display: inline-block;
  height: 100%;
  width: 32%;
  font-size: 1em;
  line-height: 30px;
  transition: all 0.2s ease-in-out;
}
.pagination-link:hover {
  font-weight: bold;
  cursor: pointer;
}
#results {
  clear: both;
}

/* pagination buttons */
.pag-buttons {
  display: block;
  margin: 2% 0;
  clear: both;
}
.pag-buttons input[type="submit"],
.pag-buttons input[type="reset"] {
  font-family: $font-list;
  display: inline-block;
  width: 20%;
  height: 30px;
  line-height: 30px;
  background-color: $color-orange;
  color: $color-white;
  border: 0;
  font-size: 1.1em;
  text-align: center;
  padding: 0;
}
.pag-buttons input[type="submit"]:hover,
.pag-buttons input[type="reset"]:hover {
  color: $color-orange;
  background-color: $color-white;
}

/* left side bar */
.left-side-content {
  position: fixed;
  top: 3em;
  z-index: 100;
  left: -205px;
  width: 205px;
  height: auto;
  background-color: $color-white;
  transition: all ease 0.2s;
}
.left-side-content.is-expanded {
  position: fixed;
  top: 3em;
  z-index: 100;
  left: 0;
  width: 205px;
  height: auto;
  background-color: $color-white;
}

/* accordian menu */
#accordian-nav {
  margin: auto;
  height: auto;
  width: 94%;
  color: $content-color1a;
  font-family: $font-list;
}
#accordian-nav span {
  width: 8%;
}
#accordian-container {
  margin: 0;
}
#accordian-container button {
  font-size: 0.9rem;
  padding: 0;
  text-align: left;
  border-radius: unset;
  text-transform: uppercase;
  font-family: $font-list;
  cursor: pointer;
  background-color: #eeeeee;
  line-height: 35px;
  height: 35px;
  text-indent: 0.2em;
}
#accordian-container button:focus {
  box-shadow: none;
}
/* Add a background to accordion panel  */
.accordion:hover,
.active {
  background-color: #cccccc;
}
/* Style the accordion panel. */
.panel {
  padding: 0;
  background-color: $color-white;
  overflow: hidden;
  transform: scaleY(1);
  transform-origin: top;
  transition: transform 0.4s ease;
  box-sizing: border-box;
}
.active::after {
  content: "\2796";

  /* Unicode character for "minus" sign (-) */
}
#accordian-container h3 {
  font-size: 0.8em;
  text-transform: uppercase;
  background-color: $content-color1;
  padding: 1em 0;
  color: $login-color;
  text-indent: 2%;
}
