    border: 0;
    line-height: 30px;
    font-size: 0.8em;
    text-transform: uppercase;
  }
  .pag-search-label:hover {
    background-color: $color-white;
    color: $color-orange;
    border: 0;
    cursor: pointer;
  }

  /* pagination */
  #right-content-section > #pagination {
    height: 30px;
    width: 35%;
    display: block;
    margin: 0 0 2%;
    float: right;
  }
  .pagination-link {
    color: $color-white;
    background-color: $color-orange;
    text-align: center;
    display: inline-block;
    height: 100%;
    width: 32%;
    font-size: 1em;
    line-height: 30px;
    transition: all 0.2s ease-in-out;
  }
  .pagination-link:hover {
    font-weight: bold;
    cursor: pointer;
  }
  #results {
    clear: both;
  }

  /* pagination buttons */
  .pag-buttons {
    display: block;
    margin: 2% 0;
    clear: both;
  }
  .pag-buttons input[type="submit"],
  .pag-buttons input[type="reset"] {
    font-family: $font-list;
    display: inline-block;
    width: 20%;
    height: 30px;
    line-height: 30px;
    background-color: $color-orange;
    color: $color-white;
    border: 0;
    font-size: 1.1em;
    text-align: center;
    padding: 0;
  }
  .pag-buttons input[type="submit"]:hover,
  .pag-buttons input[type="reset"]:hover {
    color: $color-orange;
    background-color: $color-white;
  }

  /* left side bar */
  .left-side-content {
    position: fixed;
    top: 3em;
    z-index: 100;
    left: -205px;
    width: 205px;
    height: auto;
    background-color: $color-white;
    transition: all ease 0.2s;
  }
  .left-side-content.is-expanded {
    position: fixed;
    top: 3em;
    z-index: 100;
    left: 0;
    width: 205px;
    height: auto;
    background-color: $color-white;
  }

  /* accordian menu */
  #accordian-nav {
    margin: auto;
    height: auto;
    width: 94%;
    color: $content-color1a;
    font-family: $font-list;
  }
  #accordian-nav span {
    width: 8%;
  }
  #accordian-container {
    margin: 0;
  }
  #accordian-container button {
    font-size: 0.9rem;
    padding: 0;
    text-align: left;
    border-radius: unset;
    text-transform: uppercase;
    font-family: $font-list;
    cursor: pointer;
    background-color: #eeeeee;
    line-height: 35px;
    height: 35px;
    text-indent: 0.2em;
  }
  #accordian-container button:focus {
    box-shadow: none;
  }
  /* Add a background to accordion panel  */
  .accordion:hover,
  .active {
    background-color: #cccccc;
  }
  /* Style the accordion panel. */
  .panel {
    padding: 0;
    background-color: $color-white;
    overflow: hidden;
    transform: scaleY(1);
    transform-origin: top;
    transition: transform 0.4s ease;
    box-sizing: border-box;
  }
  .active::after {
    content: "\2796";

    /* Unicode character for "minus" sign (-) */
  }
  #accordian-container h3 {
    font-size: 0.8em;
    text-transform: uppercase;
    background-color: $content-color1;
    padding: 1em 0;
    color: $login-color;
    text-indent: 2%;
  }
  #accordian-container ul {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    display: block;
    font-family: $font-list;
  }
  #accordian-container ul li {
    line-height: 35px;
    display: block;
    height: 35px;
    font-size: 1em;
    width: 100%;
    text-align: left;
    text-indent: 0.5em;
  }
  #accordian-container ul li:hover {
    font-weight: bold;
    cursor: pointer;
  }

  /* Home page search form */
  #front-page-form-container {
    width: 94%;
    height: 330px;
    margin: auto;
  }
  #front-page-form {
    width: 100%;
    height: 320px;
    margin: auto;
    font-family: $font-list;
  }
  #front-page-form h1 {
    font-size: 1.5em;
    background: none;
    color: $color-white;
    width: 100%;
    text-align: center;
    line-height: 30px;
    height: 90px;
    text-indent: 0;
    letter-spacing: -0.08em;
    padding-top: 0.3em;
  }
  #front-page-form select {
    font-family: $font-list;
    width: 90%;
    height: 35px;
    font-size: 1em;
    margin: 0.5em auto;
    color: $content-color;
    display: block;
  }
  .find-shoes {
    font-family: $font-list;
    width: 60%;
    height: 40px;
    font-size: 1.1em;
    line-height: 40px;
    margin: auto;
    background-color: $color-orange;
    display: block;
    color: $color-white;
    border: 0;
    outline: none;
  }
  .find-shoes:hover {
    color: $color-orange;
    display: block;
    background-color: $color-white;
    border: 0;
    outline: none;
    cursor: pointer;
  }

  .spacer {
    clear: both;
    height: 1px;
  }

  /* Footer */
  .footer-nav {
    clear: both;
    height: 50px;
    width: 100%;
  }
  .footer-inner-nav {
    list-style: none;
    margin: auto;
    height: 50px;
    width: 100%;
    padding: 0;
  }
  .footer-inner-nav-item {
    float: left;
    width: 16.5%;
    height: 50px;
    margin: 0;
    text-align: center;
    display: block;
  }
  .footer-inner-nav-link {
    text-align: center;
    line-height: 15px;
    height: auto;
    color: $color-black;
    display: block;
    font-size: 0.55em;
    text-transform: uppercase;
  }
  .footer-inner-nav-link:hover {
    color: $color-orange;
    line-height: 15px;
    font-weight: bold;
  }

  .copyr {
    margin: 2% auto;
    font-size: 0.6em;
    width: 50%;
    letter-spacing: 0.1em;
  }
}

@media only screen and (min-width: 480px) and (max-width: 735px) {
}

@media only screen and (min-width: 360px) and (max-width: 479px) {
  #wrapper {
    font-family: $font-list;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    padding: 0 0.5em;
    display: block;
    background-color: $color-white;
  }

  /* toggle butttons */
  #sidebar-toggle-btn {
    display: block;
    position: fixed;
    left: 80%;
    z-index: 100;
    top: 40vh;
    width: 65px;
    height: 100px;
    background-color: $color-white;
    border: 2px solid $color-orange;
    color: $color-orange;
    line-height: 100px;
    text-align: center;
    font-size: 1em;
  }

  #sidebar-toggle-btn:hover {
    background-color: $color-orange;
    cursor: pointer;
    border: 2px solid $header-color;
    color: $color-white;
    font-weight: bold;
  }

  #menu-toggle-btn {
    display: block;
    position: fixed;
    top: 0.25em;
    left: 80%;
    z-index: 4;
    width: 20%;
    height: 45px;
    line-height: 45px;
    text-align: center;
    background-color: $color-orange;
    border: 2px solid $color-black;
    color: $color-black;
    font-size: 0.9em;
  }

  #menu-toggle-btn:hover {
    background-color: $color-black;
    cursor: pointer;
    color: $color-white;
    border: none;
  }

  /* site header */
  #site-header {
    height: 70px;
    width: 100%;
  }

  /* logo */
  #logo {
    width: 60%;
    margin: 0;
    padding: 0;
  }

  /* site navigation */
  #main-nav {
    height: 30px;
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0 0 0.5em;
  }

  /* signin box */
  #signin-box {
    width: auto;
    display: none;
    float: right;
    height: 20px;
  }
  #signin-box > .signin-box-link {
    display: inline-block;
    height: 100%;
    line-height: 20px;
    width: auto;
    font-size: 0.7em;
    text-decoration: none;
    border-right: 1px solid $login-color;
    text-align: center;
    color: $login-color;
    letter-spacing: 0.01em;
    padding: 0 0.5em;
  }
  .signin-box-link:hover {
    color: $color-orange;
    font-weight: bold;
  }
  .signin-box-link:last-of-type {
    border: none;
  }

  /* inner navigation */
  #inner-nav {
    display: none;
    width: 75%;
    padding: 0;
    height: 40px;
    margin: 0;
  }
  #inner-nav > .inner-nav-item {
    float: left;
    width: auto;
    height: 40px;
    margin-right: 0.2em;
    text-align: center;
    display: block;
  }
  .inner-nav-link {
    padding: 0 0.5em;
    text-align: center;
    height: 40px;
    color: $color-black;
    line-height: 40px;
    display: block;
    transition: all ease 0.2s;
    text-transform: uppercase;
    font-size: 0.8em;
  }
  .inner-nav-link:focus {
    background-color: rgb(255 255 255 / 40%);
    text-decoration: none;
    display: block;
    border-radius: 3px 3px 0 0;
    box-shadow: 0 -2px 2px 0 rgb(0 0 0 / 20%);
