  .inner-nav-link:hover {
    background-color: rgb(255 255 255 / 40%);
    text-decoration: none;
    display: block;
    border-radius: 3px 3px 0 0;
    box-shadow: 0 -2px 2px 0 rgb(0 0 0 / 20%);
  }
  #inner-nav > .selected {
    a:link,
    a:visited,
    a:hover {
      background-color: rgb(255 255 255);
    }
  }

  /* mobile navigation */
  .mobile-nav.is-expanded {
    top: 0;
    display: block;
    width: 95%;
    background: $color-black;
    position: fixed;
    z-index: 4;
    border: 1px solid $color-black;
    height: 290px;
    padding: 1%;
  }
  .mobile-nav {
    transition: all ease 0.2s;
    display: block;
    width: 95%;
    background: $color-black;
    position: fixed;
    z-index: 4;
    border: 1px solid $color-black;
    height: 290px;
    top: -290px;
    padding: 1%;
  }
  .mobile-nav .mobile-nav-item {
    width: 100%;
    display: block;
    height: 44px;
    float: none;
    margin-bottom: 0.15em;
  }
  .mobile-nav-item .mobile-nav-link {
    font-size: 1.1em;
    line-height: 45px;
    width: 100%;
    display: block;
    float: none;
    text-align: center;
    color: $color-black;
    background-color: $color-orange;
    text-decoration: none;
  }
  .mobile-nav-item .mobile-nav-link:hover {
    text-decoration: none;
    display: block;
    border-radius: none;
    box-shadow: none;
    background-color: $color-black;
    color: $color-white;
    border-bottom: none;
  }
  .mobile-nav-item .mobile-nav-link:visited {
    background-color: $color-white;
    color: $color-black;
  }

  /* select search categories */
  #search-category {
    display: block;
    height: auto;
    width: 100%;
    padding: 0;
    margin: 1% 0;
    clear: both;
  }
  .search-category-btn {
    width: 100%;
    font-family: $font-list;
    height: 35px;
    line-height: 35px;
    font-size: 1.25em;
    margin: 0;
    background-color: $color-black;
    color: $color-white;
    border: 0;
    padding: 0;
    text-align: center;
    transition: all ease-in-out 0.2s;
    font-weight: bold;
  }
  .search-category-btn:hover {
    background-color: $color-white;
    color: $color-black;
    cursor: pointer;
    font-weight: bold;
  }
  #search-category-form {
