    width: 100%;
    font-size: 0.85em;
    margin: 0.25em 0;
    color: $content-color2;
    line-height: 35px;
    height: 35px;
  }

  /* main content */
  #main-content {
    height: auto;
    width: 100%;
    margin: 0;
    font-family: $font-list;
  }

  /* main content header */
  #main-content-title {
    background-color: $content-color;
    font-family: $font-list;
    width: 100%;
    margin: 1% 0 0;
    height: 40px;
    color: $color-white;
    font-size: 1.2em;
    text-indent: 1%;
    line-height: 40px;
    text-transform: uppercase;
    font-weight: bold;
