  .other-product-box-caption > .other-product-box-price {
    height: auto;
    line-height: 25px;
    margin: 0;
    font-weight: bold;
    width: 100%;
    text-indent: 0.2em;
    flex: 1 0 auto;
  }
  /* Price Icon */
  .other-product-box-caption > .other-product-cart-icon {
