  #main-inner-content {
    width: 100%;
    margin: 0;
    font-family: $font-list;
    float: left;
    padding: 0 0.75em;
  }

  /* inner content title */
  .main-inner-content-title {
    font-weight: bold;
    color: $color-orange;
    font-size: 1em;
    height: auto;
    line-height: 25px;
    margin-bottom: 1%;
  }

  /* rightside content section */
  #right-content-section {
    width: 100%;
    margin: 0 0 20px;
  }
