  .right-content-section-header {
    height: 40px;
    background: none;
    color: $content-color2;
    margin: 0;
    font-size: 1.2em;
    line-height: 40px;
  }
  /* front page banner */
  #front-page-banner {
    position: relative;
    height: auto;
    width: 100%;
    margin: 0;
    padding: 0.25em;
    border: solid 1px $content-color1a;
