    display: block;
    width: 100%;
  }
  .banner-nav-link {
    background-color: $color-orange;
    display: block;
    height: auto;
    width: 100%;
    margin-bottom: 0.2em;
    color: $color-white;
    font-size: 0.65em;
    line-height: 22px;
    text-align: center;
    transition: all ease 0.2s;
    text-transform: uppercase;
    border-radius: 5px;
    text-decoration: none;
  }
  .banner-nav-link:hover {
    background-color: $color-white;
    color: $color-orange;
    font-weight: bold;
    text-align: center;
  }

  /* Products Boxes */
  #right-content-section > .product-boxes {
    height: auto;
    width: 100%;
    font-family: $font-list;
    display: flex;
    flex-flow: column;
    align-items: flex-start;
    justify-content: center;
  }
  /* product box */
  .product-boxes .product-box {
    padding: 0.1em;
    height: auto;
    flex: 0 1 auto;
    width: auto;
    font-family: $font-list;
    margin: 15px auto;
  }
  /* product box image */
  .product-box .product-box-img {
    width: 100%;
    display: block;
    margin: 0 auto;
    height: auto;
  }
  /* product box caption */
  .product-box .product-box-caption {
    width: 100%;
    margin: 0;
    padding: 7% 0 0;
    background-image: url("../images/product-info-bg.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    position: relative;
    display: flex;
    flex-flow: row wrap;
  }
  .product-box-title {
    color: $login-color;
    line-height: 25px;
    text-transform: uppercase;
    font-size: 85%;
    margin: 0;
    text-indent: 0.2em;
    flex: 1 0 auto;
  }
  .product-box-price {
    height: auto;
    line-height: 25px;
    margin: 0;
    font-weight: bold;
    width: 100%;
    text-indent: 0.2em;
    flex: 1 0 auto;
  }
  .product-box-icon-link {
    width: 15%;
    position: relative;
  }
  /* Price Icon */
  .product-box-icon {
    display: block;
    transition: all 0.3s ease;
    position: absolute;
    left: 80%;
    top: 20%;
  }
  .product-box-icon:hover {
    transform: rotate(360deg);
    cursor: pointer;
  }

  /* Wider Products Boxes */
  #right-content-section > .larger-product-boxes {
    height: auto;
    width: 100%;
    display: flex;
    flex-flow: column;
    align-items: flex-start;
    justify-content: center;
  }
  .larger-product-box {
    display: flex;
    flex-flow: column;
    width: 100%;
    font-family: $font-list;
    margin-bottom: 15px;
  }
  .larger-product-box-image {
    width: auto;
    display: block;
    margin: 1em auto;
    height: auto;
  }
  .larger-product-box-caption {
    width: 100%;
    height: auto;
    margin: 0;
    padding: 1%;
    display: block;
    float: left;
  }
  .larger-product-box-title {
    background-color: $color-white;
    height: auto;
    line-height: 45px;
    text-indent: 0.5em;
    font-size: 0.9em;
    color: $login-color;
    font-weight: bold;
  }
  .larger-product-box-text {
    width: 100%;
    height: auto;
    display: block;
    line-height: 1.5em;
    text-indent: 0;
    margin: 0;
    font-size: 0.8em;
    padding: 0;
  }
  .larger-product-box-price {
    height: auto;
    line-height: 50px;
    font-size: 1em;
    margin: 0;
    font-weight: bold;
    display: block;
    width: 100%;
    text-indent: 1%;
  }
  .larger-product-box-link {
    display: block;
  }
  .larger-product-box-icon {
    float: left;
    display: block;
    margin: 0;
    width: auto;
    transition: all 0.3s ease;
  }
  .larger-product-box-icon:hover {
    transform: rotate(360deg);
    cursor: pointer;
  }
  .larger-product-box-price:last-of-type {
    color: $login-color;
  }

  /* pagination search */
  #right-content-section > #pag-search {
    width: 45%;
    display: block;
    float: left;
    margin: 0 0 2%;
    padding: 0;
  }
  .pag-search-select {
    font-family: $font-list;
    width: 50%;
    display: inline-block;
    height: 30px;
    margin: 0 1% 0 0;
    padding: 0;
    line-height: 30px;
    font-size: 0.85em;
  }
  .pag-search-label {
    float: left;
    width: 39%;
    display: block;
    height: 30px;
    color: $color-black;
    border: 0;
    line-height: 30px;
    font-size: 0.8em;
    text-transform: uppercase;
  }
  .pag-search-label:hover {
    background-color: $color-white;
    color: $color-orange;
    border: 0;
    cursor: pointer;
  }

  /* pagination */
  #right-content-section > #pagination {
    height: 30px;
    width: 35%;
    display: block;
    margin: 0 0 2%;
    float: right;
  }
  .pagination-link {
    color: $color-white;
    background-color: $color-orange;
    text-align: center;
    display: inline-block;
    height: 100%;
    width: 32%;
    font-size: 1em;
    line-height: 30px;
    transition: all 0.2s ease-in-out;
  }
  .pagination-link:hover {
    font-weight: bold;
    cursor: pointer;
  }
  #results {
    clear: both;
  }

  /* pagination buttons */
  .pag-buttons {
    display: block;
    margin: 2% 0;
    clear: both;
  }
  .pag-buttons input[type="submit"],
  .pag-buttons input[type="reset"] {
    font-family: $font-list;
    display: inline-block;
    width: 20%;
    height: 30px;
    line-height: 30px;
    background-color: $color-orange;
    color: $color-white;
    border: 0;
    font-size: 1.1em;
    text-align: center;
    padding: 0;
  }
  .pag-buttons input[type="submit"]:hover,
  .pag-buttons input[type="reset"]:hover {
    color: $color-orange;
    background-color: $color-white;
  }

  /* left side bar */
  .left-side-content {
    position: fixed;
    top: 3em;
    z-index: 100;
    left: -205px;
    width: 205px;
    height: auto;
    background-color: $color-white;
    transition: all ease 0.2s;
  }
  .left-side-content.is-expanded {
    position: fixed;
    top: 3em;
    z-index: 100;
    left: 0;
    width: 205px;
    height: auto;
    background-color: $color-white;
  }

  /* accordian menu */
  #accordian-nav {
    margin: auto;
    height: auto;
    width: 94%;
    color: $content-color1a;
    font-family: $font-list;
  }
  #accordian-nav span {
    width: 8%;
  }
  #accordian-container {
    margin: 0;
  }
  #accordian-container button {
    font-size: 0.9rem;
    padding: 0;
    text-align: left;
    border-radius: unset;
    text-transform: uppercase;
    font-family: $font-list;
    cursor: pointer;
    background-color: #eeeeee;
    line-height: 35px;
    height: 35px;
    text-indent: 0.2em;
  }
  #accordian-container button:focus {
    box-shadow: none;
  }
  /* Add a background to accordion panel  */
  .accordion:hover,
  .active {
    background-color: #cccccc;
  }
  /* Style the accordion panel. */
  .panel {
    padding: 0;
    background-color: $color-white;
    overflow: hidden;
    transform: scaleY(1);
    transform-origin: top;
    transition: transform 0.4s ease;
    box-sizing: border-box;
  }
  .active::after {
    content: "\2796";

    /* Unicode character for "minus" sign (-) */
  }
  #accordian-container h3 {
    font-size: 0.8em;
    text-transform: uppercase;
    background-color: $content-color1;
    padding: 1em 0;
    color: $login-color;
    text-indent: 2%;
  }
  #accordian-container ul {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    display: block;
    font-family: $font-list;
  }
  #accordian-container ul li {
    line-height: 35px;
    display: block;
    height: 35px;
    font-size: 1em;
    width: 100%;
    text-align: left;
    text-indent: 0.5em;
  }
  #accordian-container ul li:hover {
    font-weight: bold;
    cursor: pointer;
  }

  /* Home page search form */
  #front-page-form-container {
    width: 94%;
    height: 330px;
    margin: auto;
  }
  #front-page-form {
    width: 100%;
    height: 320px;
    margin: auto;
    font-family: $font-list;
  }
  #front-page-form h1 {
    font-size: 1.5em;
    background: none;
    color: $color-white;
    width: 100%;
    text-align: center;
    line-height: 30px;
    height: 90px;
    text-indent: 0;
    letter-spacing: -0.08em;
    padding-top: 0.3em;
  }
  #front-page-form select {
    font-family: $font-list;
    width: 90%;
    height: 35px;
    font-size: 1em;
    margin: 0.5em auto;
    color: $content-color;
    display: block;
  }
  .find-shoes {
    font-family: $font-list;
    width: 60%;
    height: 40px;
    font-size: 1.1em;
    line-height: 40px;
    margin: auto;
    background-color: $color-orange;
    display: block;
    color: $color-white;
    border: 0;
    outline: none;
  }
  .find-shoes:hover {
    color: $color-orange;
    display: block;
    background-color: $color-white;
    border: 0;
    outline: none;
    cursor: pointer;
  }

  .spacer {
    clear: both;
    height: 1px;
  }

  /* Footer */
  .footer-nav {
    clear: both;
    height: 50px;
    width: 100%;
  }
  .footer-inner-nav {
    list-style: none;
    margin: auto;
    height: 50px;
    width: 100%;
    padding: 0;
  }
  .footer-inner-nav-item {
    float: left;
    width: 16.5%;
    height: 50px;
    margin: 0;
    text-align: center;
    display: block;
  }
  .footer-inner-nav-link {
    text-align: center;
    line-height: 15px;
    height: auto;
    color: $color-black;
    display: block;
    font-size: 0.55em;
    text-transform: uppercase;
  }
  .footer-inner-nav-link:hover {
    color: $color-orange;
    line-height: 15px;
    font-weight: bold;
  }

  .copyr {
    margin: 2% auto;
    font-size: 0.6em;
    width: 50%;
    letter-spacing: 0.1em;
  }
}
